@import './var.scss';
@import './theme.scss';

#claim {
  position: relative;
  margin: 0 auto;
  border: none;
  background: $color-card-bg-active;
  border-radius: 10px;
  padding: 30px 40px;

  section {
    .header {
      font-size: 20px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 28px;
      margin: 0;
    }

    .subTitle {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 20px;
    }

    .mb-30 {
      margin-bottom: 30px;
    }

    .mb-20 {
      margin-bottom: 20px;
    }

    .mb-10 {
      margin-bottom: 10px;
    }

    .mt-30 {
      margin-top: 30px;
    }

    .mt-20 {
      margin-top: 20px;
    }
  }

  .about {
    background: $color-info-bg;
    border-radius: 10px;
    padding: 16px;
    li {
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 22px;
      margin-top: 16px;
      &:first-child {
        margin-top: 0;
      }
      a {
        color: $color-a;
      }
    }

    ul {
      padding-left: 16px;
      margin: 0;
      li {
        list-style: disc;
        &::marker {
          color: $color-info-marker;
        }
      }
    }

    ol {
      padding-left: 16px;
      margin: 0;
      li {
        list-style: auto;
        &::marker {
          color: $color-text-default-5;
        }
      }
    }
  }

  .claim-box {
    display: flex;
    margin-bottom: 60px;
    > section {
      width: 50%;
      &:nth-child(1) {
        margin-right: 40px;
      }
    }
  }
}

@include mobile-only {
  #claim {
    padding: 14px 10px;
    .claim-box {
      display: block;
      margin-bottom: 20px;
      > section {
        width: 100%;
        &:nth-child(1) {
          margin-bottom: 20px;
        }
      }
    }
  }
}
